import md5 from '@/md5.js'
export default {
	data() {
		return {
			tags: [],
			arealist: [],
			endlist: [{
				name: "全部",
				id: -1
			}, {
				name: "连载中",
				id: 0
			}, {
				name: "已完结",
				id: 1
			}, ],
			booklist: [],
			userInfo: {},
			tag_current: 0,
			area_current: 0,
			end_current: 0,
			loadStatus: 'loadmore',
			startlem: 0,
			pageSize: 10,
		};
	},
	onLoad: async function() {
		await this.gettags();
		await this.getarealist();
		this.initbooklist()
		this.userInfo = this.$store.state;
	},
	onShow: function() {
		// this.getbooklist();
	},
	onReachBottom() {
		this.loadStatus = 'loading';
		// 模拟数据加载
		setTimeout(() => {
			this.getbooklist();
			this.loadStatus = 'loadmore';
		}, 1000)
	},
	methods: {
		getapi() {
			var timestamp = Date.parse(new Date());
			var api_key = "abcde";
			return [timestamp, md5(api_key + timestamp)];
			//返回api_key+时间戳md5加密
		},
		async gettags() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/tags/getList',
				data: {
					time: key[0],
					token: key[1]
				},
				success: (res) => {
					this.tags = res.data.tags;
					this.tags.unshift({
						tag_name: "全部"
					});
					this.inittags();
					console.log(this.tags)
				}
			});
		},
		async getarealist() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/tags/getAreaList',
				data: {
					time: key[0],
					token: key[1]
				},
				success: (res) => {
					this.arealist = res.data.areas;
					this.arealist.unshift({
						area_name: "全部",
						id: -1
					});
					console.log(this.arealist)
				}
			});
		},
		async getbooklist() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/tags/getBookList',
				data: {
					time: key[0],
					token: key[1],
					area: this.arealist[this.area_current].id,
					tag: this.tags[this.tag_current].name,
					end: this.endlist[this.end_current].id,
					startItem: this.startlem,
					pageSize: this.pageSize
				},
				success: (res) => {
					this.booklist = this.booklist.concat(res.data.books);
					this.startlem = this.startlem + this.pageSize;
					console.log(res.data)
				}
			});
		},
		async initbooklist() {
			var key = await this.getapi();
			uni.request({
				url: 'http://www.liaowang.xyz/app/tags/getBookList',
				data: {
					time: key[0],
					token: key[1],
					area: -1,
					startlem: this.startlem,
					pageSize: this.pageSize
				},
				success: (res) => {
					console.log(res.data)
					this.booklist = this.booklist.concat(res.data.books);
					this.startlem = this.startlem + this.pageSize;
				}
			});
		},
		change_tag(index) {
			// if(index===0)
			this.tag_current = index;
			this.booklist=[];
			this.startlem=0;
			this.getbooklist();
		},
		change_area(index) {
			this.area_current = index;
			this.booklist=[];
			this.startlem=0;
			this.getbooklist();
		},
		change_end(index) {
			this.end_current = index;
			this.booklist=[];
			this.startlem=0;
			this.getbooklist();
		},
		gettname(i) {
			return this.tags[i].tag_name;
		},
		async inittags() {
			for (let i = 0; i < this.tags.length; i++) {
				if (i === 0) {
					this.tags[i].name = "";
				} else {
					this.tags[i].name = await this.gettname(i);
				}
			}
		},
		gotocomicdetails(id, uid) {
			uni.navigateTo({
				url: '../comicdetails/comicdetails?id=' + id + '&uid=' + uid
			})
		}
	}
}
